const Gan = ["", "", "", "", "", "", "", "", "", ""];
const Zhi = ["", "", "", "", "", "", "", "", "", "", "", ""];
const Wx = ["", "", "", "", ""];
const Fw = ["", "", "", "", ""];
const Shishen = ["", "", "()", "", "()", "", "", "", "", ""];
//const Animals = ["", "", "", "", "", "", "", "", "", "", "", ""];
//const solarTerm = ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""];
const solarMonth = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
const sTermInfo = [0, 21208, 42467, 63836, 85337, 107014, 128867, 150921, 173149, 195551, 218072, 240693, 263343, 285989, 308563, 331033, 353350, 375494, 397447, 419210, 440795, 462224, 483532, 504758];

/* eslint-disable */
export default class Bazi {

    /**
     *  offset , 0=
     * @param num
     * @returns {*}
     */
    static cyclical(num) {
        return (Gan[num % 10] + Zhi[num % 12]);
    }

    /**
     *  ym+1
     * @param y
     * @param m
     * @returns {*}
     */
    static solarDays(y, m) {
        if (m === 1) {
            return ((y % 4 === 0) && (y % 100 !== 0) || (y % 400 === 0)) ? 29 : 28;
        } else {
            return solarMonth[m];
        }
    }

    /**
     * n(0)
     * @param y
     * @param n
     * @returns {number}
     */
    static sTerm(y, n) {
        var offDate = new Date(( 31556925974.7 * (y - 1900) + sTermInfo[n] * 60000  ) + Date.UTC(1900, 0, 6, 2, 5));
        return (offDate.getUTCDate());
    }

    /**
     *
     * @param y
     * @param m
     * @param d
     * @constructor
     */
    static calc(objDate) {
        let y = objDate.getFullYear(),
            m = objDate.getMonth(),
            d = objDate.getDate(),
            h = objDate.getHours();

        var cY, cM, cD; //,,

        //////// 1900(6036)
        if (m < 2) {
            cY = this.cyclical(y - 1900 + 36 - 1);
        } else {
            cY = this.cyclical(y - 1900 + 36);
        }
        var term2 = this.sTerm(y, 2); //
        //, 
        if (m === 1 && d >= term2) cY = this.cyclical(y - 1900 + 36);

        //////// 19001 (6012)
        var firstNode = this.sTerm(y, m * 2) //
        cM = this.cyclical((y - 1900) * 12 + m + 12);
        //, 
        if (d >= firstNode) cM = this.cyclical((y - 1900) * 12 + m + 13);


        // 1900/1/1 
        //1900/1/1 1970/1/1 25567, 1900/1/1 (6010)
        var dayCyclical = Date.UTC(y, m, 1, 0, 0, 0, 0) / 86400000 + 25567 + 10;
        //
        cD = this.cyclical(dayCyclical + d - 1);

        let json = {};

        json.nz = cY;
        json.yz = cM;
        json.rz = cD;
        json.sz = this.calcSz(cD, h);

        //
        json.bz = json.nz + "" + json.yz + "" + json.rz + "" + json.sz;
        //
        json.wx = this.calcWxFw(json.nz).wx + "" + this.calcWxFw(json.yz).wx + "" + this.calcWxFw(json.rz).wx + "" + this.calcWxFw(json.sz).wx;
        //
        json.fw = this.calcWxFw(json.nz).fw + "" + this.calcWxFw(json.yz).fw + "" + this.calcWxFw(json.rz).fw + "" + this.calcWxFw(json.sz).fw;
        //
        json.shishen = this.calcShiShen(json.rz, json.nz) + "" + this.calcShiShen(json.rz, json.yz) + "" + this.calcShiShen(json.rz, json.sz);
        return json;
    }

    /**
     *  
     * 
     * 
     * 
     * 
     * 
     * 
     * @param m
     */
    static calcSz(rz, hour) {
        let rg = rz.substr(0, 1);
        let st = 0;
        let x = Math.ceil(hour / 2) % 12; //
        if (rg === "" || rg === "") {
            st = 1; //"";
        } else if (rg === "" || rg === "") {
            st = 3; //"";
        } else if (rg === "" || rg === "") {
            st = 5;//"";
        } else if (rg === "" || rg === "") {
            st = 7;//"";
        } else if (rg === "" || rg === "") {
            st = 9;//"";
        }
        return Gan[(x + st - 1) % 10] + Zhi[x];
    }

    /**
     * 
     * @param hour
     * @returns {string}
     */
    static cHour(hour) {
        let x = Math.ceil(hour / 2) % 12;
        return Zhi[x];
    }

    /**
     *  
     *  
     *  
     *  
     *  
     * 
     */
    static calcWxFw(gz) {
        let json = {};
        let tg = gz.substr(0, 1);
        let tgi = Gan.indexOf(tg);
        json.fw = Fw[((tgi/2) | 0) % 5];
        json.wx = Wx[((tgi/2) | 0) % 5];

        let dz = gz.substr(1, 1);
        switch (dz) {
            case "" :
            case "" : json.wx +=""; json.fw += ""; break;
            case "" :
            case "" : json.wx +=""; json.fw += ""; break;
            case "" :
            case "" : json.wx +=""; json.fw += ""; break;
            case "" :
            case "" : json.wx +=""; json.fw += ""; break;
            default : json.wx +=""; json.fw += "";
        }

        return json;
    }

    /**
     * 
     *  =-+13+310
     * @param rz
     * @param tz
     */
    static calcShiShen(rz, tz) {
        // 
        let rg = rz.substr(0, 1);
        let rgi = Gan.indexOf(rg) + 1;
        // 
        let tg = tz.substr(0, 1);
        let tgi = Gan.indexOf(tg) + 1;
        let x = 1;
        if (rgi % 2 === 1 && tgi % 2 === 0) {
            x = 3;
        }
        let index = rgi - tgi + x - 1;
        if (index < 0) {
            index += 10;
        } else {
            index %= 10;
        }
        return Shishen[index]
    }
}


