const lunarInfo = [
    0x04bd8, 0x04ae0, 0x0a570, 0x054d5, 0x0d260, 0x0d950, 0x16554, 0x056a0, 0x09ad0, 0x055d2,
    0x04ae0, 0x0a5b6, 0x0a4d0, 0x0d250, 0x1d255, 0x0b540, 0x0d6a0, 0x0ada2, 0x095b0, 0x14977,
    0x04970, 0x0a4b0, 0x0b4b5, 0x06a50, 0x06d40, 0x1ab54, 0x02b60, 0x09570, 0x052f2, 0x04970,
    0x06566, 0x0d4a0, 0x0ea50, 0x06e95, 0x05ad0, 0x02b60, 0x186e3, 0x092e0, 0x1c8d7, 0x0c950,
    0x0d4a0, 0x1d8a6, 0x0b550, 0x056a0, 0x1a5b4, 0x025d0, 0x092d0, 0x0d2b2, 0x0a950, 0x0b557,
    0x06ca0, 0x0b550, 0x15355, 0x04da0, 0x0a5d0, 0x14573, 0x052d0, 0x0a9a8, 0x0e950, 0x06aa0,
    0x0aea6, 0x0ab50, 0x04b60, 0x0aae4, 0x0a570, 0x05260, 0x0f263, 0x0d950, 0x05b57, 0x056a0,
    0x096d0, 0x04dd5, 0x04ad0, 0x0a4d0, 0x0d4d4, 0x0d250, 0x0d558, 0x0b540, 0x0b5a0, 0x195a6,
    0x095b0, 0x049b0, 0x0a974, 0x0a4b0, 0x0b27a, 0x06a50, 0x06d40, 0x0af46, 0x0ab60, 0x09570,
    0x04af5, 0x04970, 0x064b0, 0x074a3, 0x0ea50, 0x06b58, 0x055c0, 0x0ab60, 0x096d5, 0x092e0,
    0x0c960, 0x0d954, 0x0d4a0, 0x0da50, 0x07552, 0x056a0, 0x0abb7, 0x025d0, 0x092d0, 0x0cab5,
    0x0a950, 0x0b4a0, 0x0baa4, 0x0ad50, 0x055d9, 0x04ba0, 0x0a5b0, 0x15176, 0x052b0, 0x0a930,
    0x07954, 0x06aa0, 0x0ad50, 0x05b52, 0x04b60, 0x0a6e6, 0x0a4e0, 0x0d260, 0x0ea65, 0x0d530,
    0x05aa0, 0x076a3, 0x096d0, 0x04bd7, 0x04ad0, 0x0a4d0, 0x1d0b6, 0x0d250, 0x0d520, 0x0dd45,
    0x0b5a0, 0x056d0, 0x055b2, 0x049b0, 0x0a577, 0x0a4b0, 0x0aa50, 0x1b255, 0x06d20, 0x0ada0];

/* eslint-disable */
export default class Lunar {

    /**
     *  y
     * @param y
     * @returns {number} y 1-12, 0
     */
    static leapMonth(y) {
        //& 0xf  4
        return (lunarInfo[y - 1900] & 0xf);
    }

    /**
     * y
     * @param y
     * @returns {number} y
     */
    static leapDays(y) {
        if (this.leapMonth(y)) {
            return ((lunarInfo[y - 1900] & 0x10000) ? 30 : 29);
        } else {
            return 0;
        }
    }

    /**
     * ym
     * @param y
     * @param m 112
     * @returns {number} ym
     */
    static monthDays(y, m) {
        return (lunarInfo[y - 1900] & (0x10000 >> m)) ? 30 : 29;
    }



    /**
     * y
     * @param year
     * @returns {*} y
     */
    static lYearDays(year) {
        var i, f, sumDay, info;
        sumDay = 348; //29 X 12
        i = 0x8000; //0x8000 = 1000 0000 0000 0000
        //0x04BD8  & 0x0FFFF 124BD10
        info = lunarInfo[year - 1900] & 0x0FFFF;
        //1230
        for (var m = 0; m < 12; m++) {
            f = info & i; // 0x04BD8  & 0x0FFFF  & 0x8000[1000 0000 0000 0000]
            if (f !== 0) {
                sumDay++;
            }
            i = i >> 1;// 
        }
        return sumDay + this.leapDays(year);
    }

    /**
     * 
     * @param objDate
     */
    static calc(objDate) {
        let json = {};

        //==============================
        let i, leap = 0, temp = 0;
        //1900-1-31
        let offset = (Date.UTC(objDate.getFullYear(), objDate.getMonth(), objDate.getDate()) - Date.UTC(1900, 0, 31)) / 86400000;

        //
        for (i = 1900; i < 2050 && offset > 0; i++) {
            temp = this.lYearDays(i);
            offset -= temp;
        }

        if (offset < 0) {
            offset += temp;
            i--;
        }

        json.year = i;

        //===============================
        leap = this.leapMonth(i); //
        json.isLeap = false;

        //
        for (i = 1; i <= 12 && offset > 0; i++) {
            //
            if (leap > 0 && i === (leap + 1) && json.isLeap === false) {
                --i;
                json.isLeap = true;
                temp = this.leapDays(json.year);
            } else {
                temp = this.monthDays(json.year, i);
            }

            //
            if (json.isLeap === true && i === (leap + 1)) {
                json.isLeap = false;
            }
            offset -= temp;
        }

        //
        if (offset === 0 && leap > 0 && i === leap + 1) {
            if (json.isLeap) { //
                json.isLeap = false;
            } else {
                json.isLeap = true;
                --i;
            }
        }

        if (offset < 0) {
            offset += temp;
            --i;
        }

        json.month = i;
        json.day = offset + 1;

        json.cYear = this.cYear(json.year);
        json.cMonth = this.cMonth(json.month);
        json.cDay = this.cDay(json.day);

        json.str = json.cYear + "" + (json.isLeap ? "" : "") + json.cMonth + json.cDay;

        return json;
    }

    static cYear(y) {
        var Gan = new Array("", "", "", "", "", "", "", "", "", "");
        var Zhi = new Array("", "", "", "", "", "", "", "", "", "", "", "");

        return Gan[(y - 1900 + 6) % 10] + Zhi[(y - 1900) % 12];
    }


    static cMonth(m) {
        var nStr1 = new Array('', '', '', '', '', '', '', '', '', '', '');
        var s;
        if (m > 10) {
            s = '' + nStr1[m - 10];
        } else {
            s = nStr1[m] ;
        }
        s += '';
        return s;
    }

    static cDay(d) {
        var nStr1 = new Array('', '', '', '', '', '', '', '', '', '', '');
        var nStr2 = new Array('', '', '', '', '');
        var s = "";
        switch (d) {
            case 10: s = ''; break;
            case 20: s = ''; break;
            case 30: s = ''; break;
            default: s = nStr2[Math.floor(d / 10)]; s += nStr1[d % 10];
        }
        return (s);
    }

}



